/* Counter.css */
.counter-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.counter-box {
  border: 2px solid #333;
  padding: 20px;
  margin-bottom: 30px;
  border-radius: 10px;
}

.count {
  font-size: 24px;
  font-weight: bold;
}

.button-box {
  display: flex;
  gap: 20px;
}

.decrement-btn,
.increment-btn {
  font-size: 18px;
  padding: 10px 20px;
  border-radius: 8px;
  border: none;
  background-color: #77a5eb;
  color: white;
  cursor: pointer;
  transition: background-color 0.3s;
}

.decrement-btn:disabled,
.increment-btn:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}



.warning-message {
  color: red;
  font-size: 14px;
  margin-top: 10px;
}
